
import { defineComponent, ref, reactive, computed, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { useRouter } from 'vue-router'
import { useI18n } from 'vue-i18n'
import { ElForm } from 'element-plus'
import { FormModel } from '../packaging/types/index'
export default defineComponent({
  name: 'packaging',
  components: {},
  setup() {
    const { t } = useI18n()
    const router = useRouter()

    const dialogVisible = ref(false)
    const handleChange = (val: any) => {
      console.log(val, 'va;1')
    }
    const formModel = ref<FormModel>({
      data: [
        { unit: 'EA', qty: 1 },
        { unit: 'CS' },
        { unit: 'PL', length: 0.0, width: 0.0, height: 0.0, cube: 0.0 },
        { unit: t('other') }
      ]
    })
    onMounted(() => {
      //   console.log(
      //     router.currentRoute.value.query,
      //     'router.currentRoute.value.query'
      //   )
    })

    const formRef = ref<InstanceType<typeof ElForm>>()
    const rules = reactive({
      barcode: [{ required: true, trigger: 'change' }],
      categoryCode: [{ required: true, trigger: 'change' }]
    })
    const cancel = () => {
      formRef.value?.resetFields()
      router.push({ path: '/main/Inventory/commodityEntry/warehouse' })
    }
    const submitForm = () => {
      formRef.value?.validate((valid) => {
        console.log(valid, 'vaild')
        if (valid) {
          router.push({ path: '/main/Inventory/commodityEntry' })
        }
      })
    }
    return {
      formModel,
      rules,
      cancel,
      submitForm,
      formRef,
      t,
      dialogVisible,
      ElMessage,
      handleChange
    }
  }
})
